import React from 'react'
import {Redirect, Route, Switch} from 'react-router-dom'
import {GenderList} from './page/GenderList'
import {AddGender} from './page/AddGender'
import {EditGender} from './page/EditGender'
import GenderScreens from './Screens'

const GenderRoutes: React.FC = () => {
  return (
    <Switch>
      <Route path={GenderScreens.GENDER_LIST.PATH} component={GenderList} />
      <Route path={GenderScreens.ADD_GENDER.PATH} component={AddGender} />
      <Route path={GenderScreens.EDIT_GENDER.PATH} component={EditGender} />
      <Redirect from='/gender' exact={true} to={GenderScreens.GENDER_LIST.PATH} />
      <Redirect to={GenderScreens.GENDER_LIST.PATH} />
    </Switch>
  )
}

export default GenderRoutes
